body.about,
body.contact {
    display: flex;
    justify-content: space-between;
}

.reveal-page {
    margin: 0 auto;
    padding: 1rem 1.5rem 0 1.5rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-grow: 1;
    max-width: 1000px;
    opacity: 0;
    @media screen and (max-width: $bp-phone) {
        padding: 1rem 1rem;
        margin: 0;
    }
    @media screen and (max-width: $bp-xs) {
        padding-top: 0rem;
        margin: 0;
    }
    .page-title {
        margin: 0 auto 0 0;
        padding: 0;
        display: flex;
        position: relative;
        &:before {
            content: "";
            position: absolute;
            bottom: 0;
            height: 0.4rem;
            background: $pink;
            opacity: 0;
            left: -10px;
            right: 10px;
            animation: underline-slide 1s ease-in-out 0.5s 1 forwards;
            @media screen and (max-width: $bp-tablet) {
                margin-left: 0rem;
                height: 0.3rem;
            }
        }
        @media screen and (max-width: $bp-tablet) {
            align-self: flex-start;
            font-size: 6rem;
            padding-left: 0rem;
        }
        @media screen and (max-width: $bp-phone) {
            font-size: 5rem;
            margin-top: 0.5rem;
        }
        @media screen and (max-width: $bp-xs) {
            font-size: 3rem;
            margin-top: 0.2rem;
        }
    }
    .page-text {
        display: flex;
        flex-direction: column;
        padding-left: 0rem;
        padding-right: 2rem;
        margin-right: auto;
        .text {
            letter-spacing: 0.12rem;
            line-height: 2.3rem;
            @media screen and (max-width: $bp-phone) {
                font-size: 1.25rem;
            }
            @media screen and (max-width: $bp-xs) {
                letter-spacing: 0.07rem;
                line-height: 1.5rem;
            }
        }
    }
}
